<template>
  <div>
    <!-- FILTER -------------------------------------------------------------------------------------------------->
    <b-card no-body class="card-form mb-2">
      <b-card-header header-tag="header-filter" class="p-1" role="tab">
        <b-button block v-b-toggle.accordion-filter variant="outline-primary" size="sm">Filter</b-button>
      </b-card-header>
      <b-collapse id="accordion-filter" visible accordion="filter" role="tabpanel">
            <b-row class="mb-4 mt-2 ml-5 mr-5">
                <b-col>
                  <label for="dateFrom">From</label>
                  <b-form-datepicker
                    @input="applyFilter()"
                    :start-weekday="1"
                    class="form-control"
                    v-model="dateFromFilter"
                    id="dateFrom"
                    reset-button="true"
                    rows="1"
                  ></b-form-datepicker>
                </b-col>
              
                <b-col>
                  <label for="dateTo">To</label>
                  <b-form-datepicker
                    @input="applyFilter()"
                    :start-weekday="1"
                    class="form-control"
                    v-model="dateToFilter"
                    id="dateTo"
                    rows="1"
                    reset-button="true"
                  ></b-form-datepicker>
                </b-col>
            </b-row>
            <b-row class="mb-4 mt-2 ml-5 mr-5">
                <b-col> <label>Job</label><b-form-select @input="applyFilter()" v-model="jobFilter" :options="jobFilterOptions" ></b-form-select></b-col>
                <b-col> <label>Part</label><b-form-select @input="applyFilter()" v-model="partNumberFilter" :options="partNumberFilterOptions"></b-form-select></b-col>
                <b-col> <label>Site</label><b-form-select @input="applyFilter()" v-model="siteFilter" :options="siteFilterOptions"></b-form-select></b-col>
                <b-col> <label>Customer</label><b-form-select @input="applyFilter()" v-model="customerFilter" :options="customerFilterOptions"></b-form-select></b-col>
            </b-row>
            <b-card>
            <b-row>
              <b-col style="text-align: right;vertical-align:center;">
                  <b-label class="numberTitleBig mr-1">{{ numberClosedJobs }}</b-label>Closed jobs
                  <b-label class="numberTitleBig mr-1 ml-5">{{ numberOpenedJobs }}</b-label> Opened jobs
                  <b-label class="numberTitleBig mr-1 ml-5">{{ numberInspections }}</b-label> Inspections
                </b-col>
              </b-row>
            </b-card>
      </b-collapse>
    </b-card>  
    <div  v-if="loading==1" class="text-center text-primary mt-4">
            <b-spinner class="align-middle mr-2"></b-spinner>loading
    </div>
    <div v-if="loading==0">
      <b-tabs content-class="mt-3">
        <b-tab style="text-align: center" title="Num. parts" active>
          <b-row class="justify-content-center">
            <v-chart ref="chart2" :option="dataDefectChart" class="chart"/> 
            <b-row><b-col style="text-align: right"><b-button variant="primary" class="mt-3" @click="exportChart(2)"><i class="fa fa-print"></i></b-button></b-col></b-row>    
          </b-row>
        </b-tab>
        <b-tab title="Num. Parts by date" >
          <b-row class="justify-content-center">
            <v-chart  ref="chart1" :option="dataInspectionChart" class="chart"/>
            <b-row><b-col style="text-align: right"><b-button variant="primary" class="mt-3" @click="exportChart(1)"><i class="fa fa-print"></i></b-button></b-col></b-row>
          </b-row>
        </b-tab>
        <b-tab title="Num. parts by job">
          <b-row class="justify-content-center">
            <v-chart ref="chart3" :option="dataDefectChartJob" class="chart"/>
            <b-row><b-col style="text-align: right"><b-button variant="primary" class="mt-3" @click="exportChart(3)"><i class="fa fa-print"></i></b-button></b-col></b-row>
          </b-row>
        </b-tab>
        <b-tab title="Pareto NOK" >
          <b-row class="justify-content-center">
            <v-chart ref="chart4" :option="dataParetoNokChart" class="chart"/>
            <b-row><b-col style="text-align: right"><b-button variant="primary" class="mt-3" @click="exportChart(4)"><i class="fa fa-print"></i></b-button></b-col></b-row>
          </b-row>
        </b-tab>
        <b-tab title="Pareto RWK">
          <b-row class="justify-content-center">
            <v-chart ref="chart5" :option="dataParetoRwkChart" class="chart"/>
            <b-row><b-col style="text-align: right"><b-button variant="primary" class="mt-3" @click="exportChart(5)"><i class="fa fa-print"></i></b-button></b-col></b-row>
          </b-row>
        </b-tab>
      </b-tabs>
    </div>
  </div>
</template>

<script>
import EreportService from '../services/ereport.service';
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { BarChart, LineChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
} from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";
import jsPDF from 'jspdf';


use([
  CanvasRenderer,
  BarChart,
  LineChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent
]);

export default {
  name: "DashBoard",
  provide: {
    [THEME_KEY]: "white"
  },
  data() {
    return {
      itemsInspection:[],
      itemsJob:[],
      loading:0,
      //--------------------------------
        dataDefectChart: {
        title: {
          text: 'Number of parts',
          subtext: 'Classified in ok, nok and reworked'
        },
        tooltip: {
          trigger: 'item',
          show: true
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          data: ['OK','NOK','RWK']
        },
        yAxis: {},
        series: [
            {
              name: 'Number of parts',
              type: 'bar',
              stack: 'Total',
              label: {
                show: true,
                position: 'inside'
              },
              data: [],
            }
          ]
        },
        dataDefectChartJob: {
        title: {
          text: 'Number of parts by job',
          subtext: 'Classified in ok, nok and reworked'
        },
        tooltip: {
          trigger: 'axis',
          show: true
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisPointer: {
          type: 'shadow'
          },
          data: []
        },
        yAxis: {},
        series: [
          {name: 'OK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: []},
          {name: 'NOK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: []},
          {name: 'RWK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: []}
        ]
        },
        dataParetoNokChart: {
        title: {
          text: 'Pareto NOK',
          subtext: 'Number of NOK per defect'
        },
        tooltip: {
          trigger: 'axis',
          show: true
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top:'15%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLabel: { interval: 0, rotate: 60 },
          axisPointer: {
          type: 'shadow'
          },
          data: []
        },
        yAxis: [{
            type: 'value',
            name: 'Number of Nok',
            position: 'left'
        },
        {
            type: 'value',
            name: '%',
            position: 'right'
        }] ,
        series: [
          {name: 'Num.NOK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: [],yAxisIndex: 0},
          {name: '% NOK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: [],yAxisIndex: 1},
          {name: '% Total', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: [],yAxisIndex: 1}
        ]
        },
        dataParetoRwkChart: {
        title: {
          text: 'Pareto RWK',
          subtext: 'Number of RWK per defect'
        },
        tooltip: {
          trigger: 'axis',
          show: true
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top:'15%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLabel: { interval: 0, rotate: 60 },
          axisPointer: {
          type: 'shadow'
          },
          data: []
        },
        yAxis: [{
            type: 'value',
            name: 'Number of Rwk',
            position: 'left'
        },
        {
            type: 'value',
            name: '%',
            position: 'right'
        }] ,
        series: [
          {name: 'Num.RWK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: [],yAxisIndex: 0},
          {name: '% NOK', barGap: '10%',type: 'bar',label: {show: true,position: 'inside'},data: [],yAxisIndex: 1}
        ]
        },
        dataInspectionChart: {
        title: {
          text: 'Num. Parts by date',
          subtext: 'Number of parts by date'
        },
        tooltip: {
          trigger: 'axis',
          show: true
        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top:'10%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          axisLabel: { interval: 0, rotate: 90 },
          axisPointer: {
          type: 'shadow'
          },
          data: []
        },
        yAxis: {},
        series: [
          {name: 'Num. Parts', barGap: '10%',type: 'line',label: {show: true,position: 'inside'},data: []},
        ]
        },
        //----------------------------- FILTER
        resultFilterInspection:[],
        resultFilterJob:[],
        dateFromFilter:'',
        dateToFilter:'',
        jobFilter:'',
        siteFilter:'',
        customerFilter:'',
        partNumberFilter:'',
        jobFilterOptions:[''],
        partNumberFilterOptions:[''],
        siteFilterOptions:[''],
        customerFilterOptions:[''],
        defectFilterOptions:[],
        stateFilterOptions:['OPENED','CLOSED'],
        //-----------------------------------
        numberOpenedJobs:'',
        numberClosedJobs:'',
        numberInspections:''
      };
    },
    methods: {
        printScreen()
        {
          window.print();
        },
        getData()
        { 
          this.loading = 1;

          //JOBS
          EreportService.getData('reporting/job').then(data => {
                this.itemsJob = data;
                for(var i=0; i < this.itemsJob.length;i++ )
                {
                  this.jobFilterOptions.push(this.itemsJob[i].jobNumber);
                }
                this.jobFilterOptions.sort((a, b) => a.localeCompare(b));
                this.jobFilterOptions = this.jobFilterOptions.filter((value, index) => this.jobFilterOptions.indexOf(value) === index); // Remove duplicates

               this.numberOpenedJobs = this.itemsJob.filter(item => item.status === 'UPDATED' || item.status === 'CREATED').length;
               this.numberClosedJobs = this.itemsJob.filter(item => item.status === 'CLOSED').length;

                  //INSPECTIONS
                    EreportService.getData('reporting/inspection').then(data => {
                      this.itemsInspection = data;
                      for(var i=0; i < this.itemsInspection.length;i++ )
                      {
                        this.partNumberFilterOptions.push(this.itemsInspection[i].partInspected.number);
                        
                        if(!this.siteFilterOptions.includes(this.itemsInspection[i].jobInspected.site.name)){this.siteFilterOptions.push(this.itemsInspection[i].jobInspected.site.name);}
                        if(!this.customerFilterOptions.includes(this.itemsInspection[i].jobInspected.customer.name)){this.customerFilterOptions.push(this.itemsInspection[i].jobInspected.customer.name);}

                        for(var j=0; j < this.itemsInspection[i].defects.length;j++)
                        {
                          this.defectFilterOptions.push(this.itemsInspection[i].defects[j].description);
                        }
                      }
                      this.partNumberFilterOptions.sort((a, b) => a.localeCompare(b));
                      this.partNumberFilterOptions = this.partNumberFilterOptions.filter((value, index) => this.partNumberFilterOptions.indexOf(value) === index); // Remove duplicates

                      this.defectFilterOptions.sort((a, b) => a.localeCompare(b));
                      this.defectFilterOptions = this.defectFilterOptions.filter((value, index) => this.defectFilterOptions.indexOf(value) === index); // Remove duplicates

                      this.numberInspections = this.itemsInspection.length;

                      this.applyFilter();
                    }).catch(error => this.$parent.catchError(error));

          }).catch(error => this.$parent.catchError(error));

        },
        applyFilter()
        {
            this.loading = 1;
            // Resume ----------------------------------------------------------------------------------
            this.resultFilterInspection =  this.itemsInspection;
            if(this.dateFromFilter != '')
            {
              this.resultFilterInspection =   this.resultFilterInspection.filter(item => new Date(item.inspectionDate) >= new Date(this.dateFromFilter)); 
            }
            if(this.dateToFilter != '')
            {
              this.resultFilterInspection =   this.resultFilterInspection.filter(item => new Date(item.inspectionDate) <= new Date(this.dateToFilter)); 
            }
            if(this.jobFilter != '')
            {
              this.resultFilterInspection =   this.resultFilterInspection.filter(item => item.jobInspected.jobNumber == this.jobFilter); 
            }
            if(this.partNumberFilter != '')
            {
              this.resultFilterInspection =   this.resultFilterInspection.filter(item => item.partInspected.number == this.partNumberFilter); 
            }
            if(this.siteFilter != '')
            {
              this.resultFilterInspection =  this.resultFilterInspection.filter(item => item.jobInspected.site.name == this.siteFilter); 
            }
            if(this.customerFilter != '')
            {
              this.resultFilterInspection =   this.resultFilterInspection.filter(item => item.jobInspected.customer.name == this.customerFilter); 
            }

            this.numberOpenedJobs = this.itemsJob.filter(item => item.status === 'UPDATED' || item.status === 'CREATED').length;
            this.numberClosedJobs = this.itemsJob.filter(item => item.status === 'CLOSED').length;
            this.numberInspections = this.resultFilterInspection.length;

            // Number Inspections  ----------------------------------------------------------------------------------
            var chartValuesInspections = [];
            for(var i5 = 0;i5 < this.resultFilterInspection.length;i5++)
            {
                if(chartValuesInspections.filter(item => item.inspectionDate.getTime() == (new Date(this.resultFilterInspection[i5].inspectionDate)).getTime()).length == 0)
                {
                  chartValuesInspections.push({"inspectionDate": new Date(this.resultFilterInspection[i5].inspectionDate), "value":this.resultFilterInspection[i5].total});
                }
                else
                {
                  chartValuesInspections = chartValuesInspections.map(function(obj) {
                  if (obj.inspectionDate.getTime() == (new Date(this.resultFilterInspection[i5].inspectionDate).getTime())) {
                    return Object.assign({}, obj, {value: obj.value + this.resultFilterInspection[i5].total});
                    } else {
                      return obj;
                    }
                  },this);
                }
              }
            
            this.dataInspectionChart.xAxis.data = [];
            this.dataInspectionChart.series[0].data = [];
            chartValuesInspections.sort(function(a, b) {
              return a.inspectionDate - b.inspectionDate;
            });

            for(var k5 = 0;k5 < chartValuesInspections.length;k5++)
            {
                  this.dataInspectionChart.xAxis.data.push(chartValuesInspections[k5].inspectionDate.toLocaleDateString("en-GB", { year: "numeric",month: "2-digit",day: "2-digit",}));
                  this.dataInspectionChart.series[0].data.push({"value":chartValuesInspections[k5].value,"itemStyle":{"color": '#FFC107', "opacity": 0.8}}); 
            }
            // Defect Chart ----------------------------------------------------------------------------------
            var ok = 0;
            var nok = 0;
            var rwk = 0;

            for(var i1 = 0;i1 < this.resultFilterInspection.length;i1++)
            {
              ok = ok + this.resultFilterInspection[i1].ok;
              nok = nok + this.resultFilterInspection[i1].nok;
              rwk = rwk + this.resultFilterInspection[i1].rwk;
            }
            this.dataDefectChart.series[0].data = [];
            this.dataDefectChart.series[0].data.push({"value":ok,"itemStyle":{"color": "#28A745", "opacity": 0.8}});
            this.dataDefectChart.series[0].data.push({"value":nok,"itemStyle":{"color": "#DC3545", "opacity": 0.8}});
            this.dataDefectChart.series[0].data.push({"value":rwk,"itemStyle":{"color": "#FFC107", "opacity": 0.8}});

            // Defect Chart Jobs ----------------------------------------------------------------------------------
            var chartValues = [];
            for(var i2 = 0;i2 < this.resultFilterInspection.length;i2++)
            {
              if(chartValues.filter(item => item.jobNumber === this.resultFilterInspection[i2].jobInspected.jobNumber).length == 0)
              {
                chartValues.push({"jobNumber":this.resultFilterInspection[i2].jobInspected.jobNumber, "values":{"ok":0,"nok":0,"rwk":0}});
              }


              //for(var j2 = 0;j2 < this.resultFilterInspection[i2].defects.length;j2++)
             // {
                chartValues = chartValues.map(function(obj) {
                  if (obj.jobNumber === this.resultFilterInspection[i2].jobInspected.jobNumber) {
                    return Object.assign({}, obj, {values: {"ok": (obj.values.ok + this.resultFilterInspection[i2].ok),
                                                            "nok": (obj.values.nok + this.resultFilterInspection[i2].nok),
                                                            "rwk": (obj.values.rwk + this.resultFilterInspection[i2].rwk)}});
                  } else {
                    return obj;
                  }
                },this);
              //}
            }

            this.dataDefectChartJob.xAxis.data = [];
            this.dataDefectChartJob.series[0].data = [];
            this.dataDefectChartJob.series[1].data = []; 
            this.dataDefectChartJob.series[2].data = [];
            for(var k2 = 0;k2 < chartValues.length;k2++)
            {
              this.dataDefectChartJob.xAxis.data.push(chartValues[k2].jobNumber);
              this.dataDefectChartJob.series[0].data.push({"value":chartValues[k2].values.ok,"itemStyle":{"color": '#28A745', "opacity": 0.8}});      
              this.dataDefectChartJob.series[1].data.push({"value":chartValues[k2].values.nok,"itemStyle":{"color": '#DC3545', "opacity": 0.8}});     
              this.dataDefectChartJob.series[2].data.push({"value":chartValues[k2].values.rwk,"itemStyle":{"color": '#FFC107', "opacity": 0.8}});
            }
            // Pareto NOK  ----------------------------------------------------------------------------------
            var chartValuesParetoNOK = [];
            var total = 0;
            for(var i3 = 0;i3 < this.resultFilterInspection.length;i3++)
            {
              console.log("TOTAL:"+this.resultFilterInspection[i3].total)
              total = total + this.resultFilterInspection[i3].total;
              for(var j3 = 0;j3 < this.resultFilterInspection[i3].defects.length;j3++)
              {
                if(chartValuesParetoNOK.filter(item => item.defectDescription === this.resultFilterInspection[i3].defects[j3].description).length == 0)
                {
                  chartValuesParetoNOK.push({"defectDescription":this.resultFilterInspection[i3].defects[j3].description, "value":this.resultFilterInspection[i3].defects[j3].qtyNok});
                }
                else
                {
                  chartValuesParetoNOK = chartValuesParetoNOK.map(function(obj) {
                  if (obj.defectDescription === this.resultFilterInspection[i3].defects[j3].description) {
                    return Object.assign({}, obj, {value: obj.value + this.resultFilterInspection[i3].defects[j3].qtyNok});
                    } else {
                      return obj;
                    }
                  },this);
                }
              }
            }
            this.dataParetoNokChart.xAxis.data = [];
            this.dataParetoNokChart.series[0].data = [];
            this.dataParetoNokChart.series[1].data = [];
            this.dataParetoNokChart.series[2].data = [];
            chartValuesParetoNOK.sort(function(a, b) {
              return b.value - a.value;
            });
            
            var total3 = 0;
            for(var l3 = 0;l3 < chartValuesParetoNOK.length;l3++){ total3 = total3 + chartValuesParetoNOK[l3].value;}

            
            for(var k3 = 0;k3 < 15;k3++)
            {
             
              if(chartValuesParetoNOK[k3] != undefined)
              {
                if(chartValuesParetoNOK[k3].value > 0){
                  this.dataParetoNokChart.xAxis.data.push(chartValuesParetoNOK[k3].defectDescription);
                  this.dataParetoNokChart.series[0].data.push({"value":chartValuesParetoNOK[k3].value,"itemStyle":{"color": '#DC3545', "opacity": 0.8}});
                  this.dataParetoNokChart.series[1].data.push({"value":Math.round((chartValuesParetoNOK[k3].value/total3)*100 * 100) / 100 ,"itemStyle":{"color": '#007BFF', "opacity": 0.8}});
                  this.dataParetoNokChart.series[2].data.push({"value":Math.round((chartValuesParetoNOK[k3].value/total)*100 * 100) / 100 ,"itemStyle":{"color": '#0015AA', "opacity": 0.8}});       
                  //console.log("VALOR:"+Math.round((chartValuesParetoNOK[k3].value/total)*100 * 100) / 100 );
                }
              }
            }

            // Pareto RWK  ----------------------------------------------------------------------------------
            var chartValuesParetoRWK = [];
            for(var i4 = 0;i4 < this.resultFilterInspection.length;i4++)
            {
              for(var j4 = 0;j4 < this.resultFilterInspection[i4].reworks.length;j4++)
              {
                if(chartValuesParetoRWK.filter(item => item.reworkDescription === this.resultFilterInspection[i4].reworks[j4].description).length == 0)
                {
                  chartValuesParetoRWK.push({"reworkDescription":this.resultFilterInspection[i4].reworks[j4].description, "value":this.resultFilterInspection[i4].reworks[j4].qtyRw});
                }
                else
                {
                  chartValuesParetoRWK = chartValuesParetoRWK.map(function(obj) {
                  if (obj.reworkDescription === this.resultFilterInspection[i4].reworks[j4].description) {
                    return Object.assign({}, obj, {value: obj.value + this.resultFilterInspection[i4].reworks[j4].qtyRw});
                    } else {
                      return obj;
                    }
                  },this);
                }
              }
            }
            this.dataParetoRwkChart.xAxis.data = [];
            this.dataParetoRwkChart.series[0].data = [];
            this.dataParetoRwkChart.series[1].data = [];
            chartValuesParetoRWK.sort(function(a, b) {
              return b.value - a.value;
            });
            var total4 = 0;
            for(var l4 = 0;l4 < chartValuesParetoRWK.length;l4++){ total4 = total4 + chartValuesParetoRWK[l4].value;}

            for(var k4 = 0;k4 < 15;k4++)
            {
              if(chartValuesParetoRWK[k4] != undefined)
              {
                if(chartValuesParetoRWK[k4].value > 0){
                  this.dataParetoRwkChart.xAxis.data.push(chartValuesParetoRWK[k4].reworkDescription);
                  this.dataParetoRwkChart.series[0].data.push({"value":chartValuesParetoRWK[k4].value,"itemStyle":{"color": '#FFC107', "opacity": 0.8}}); 
                  this.dataParetoRwkChart.series[1].data.push({"value":Math.round((chartValuesParetoRWK[k4].value/total3)*100 * 100) / 100 ,"itemStyle":{"color": '#007BFF', "opacity": 0.8}});      
                }
              }
            }

           this.loading = 0;
        },
        exportChart(chartRef) {
          var chartInstance = this.$refs.chart1.chart;
          if(chartRef == 1){chartInstance = this.$refs.chart1.chart;}
          if(chartRef == 2){chartInstance = this.$refs.chart2.chart;}
          if(chartRef == 3){chartInstance = this.$refs.chart3.chart;}
          if(chartRef == 4){chartInstance = this.$refs.chart4.chart;}
          if(chartRef == 5){chartInstance = this.$refs.chart5.chart;}
          const canvas = chartInstance.getRenderedCanvas({
            pixelRatio: 2,
            backgroundColor: '#fff',
          });
          const imgData = canvas.toDataURL('image/png');
          const pdf = new jsPDF('landscape');
          pdf.addImage(imgData, 'PNG', 10, 10, 250, 150);
          pdf.save('chart.pdf');
        },
    },
    computed: {
      currentUser() {
        return this.$store.state.auth.user;
      }
    },
    mounted() {
      if (!this.currentUser) {
        this.$router.push('/login');
      }
      this.getData();
    },
    components: {VChart}
};
</script>

